.menu {
  background-color: #f8f7f8;
  position: fixed;
  bottom: 0;
  padding: 5px 0 15px 0px;
  width: 100%;
  span {
    font-size: 1.4rem;
    color: #6d9d62;
  }
  .menu-text {
    margin-top: 10px;
    display: inline-block;
  }
  sup {
    top: 115%;
    right: 50%;
  }
}
.custom-filter {
  padding: 0 4px;
  border-radius: 3px;
}
.custom-collaps {
  border: none !important;
  background: transparent;
  padding: 0;
  .ant-collapse-header {
    padding: 0 !important;
    font-size: 1.5rem;
    color: #6d9d62 !important;
    background: transparent;
  }
}
.ant-collapse-borderless {
  background-color: transparent;
}
